import {ClrSelectedState} from "@clr/angular";
import ObjectInfo from './ObjectInfo';

export class TreeNode {
   private _childNodes: TreeNode[];

   get children(): TreeNode[] {
      return this._childNodes;
   }

   set children(nodes: TreeNode[]) {
      this._childNodes = !!nodes ? nodes : [];
   }

   private _clrSelectedState: ClrSelectedState = ClrSelectedState.UNSELECTED;

   get clrSelectedState(): ClrSelectedState {
      return this._clrSelectedState;
   }

   set clrSelectedState(value) {
      if (value != this._clrSelectedState) {
         this._clrSelectedState = value;
         // Timeout in order to wait for clarity to update the selected state of the children/parent
         setTimeout(() => {
            this.onClrSelectedStateChange && this.onClrSelectedStateChange(this);
         });
      }
   }

   visible: boolean;

   isExpanded: boolean = false;

   constructor(
         public objectInfo: ObjectInfo,
         public nodes: TreeNode[] = [],
         public onClrSelectedStateChange: (node: TreeNode) => any = null) {
      this.children = nodes;
   }

   public isSelected(): boolean {
      return this.clrSelectedState === ClrSelectedState.SELECTED;
   }
}