import {Component, Input} from "@angular/core";
import {VsanTestTable} from "@generated/vsan-test-table";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {VsanTestRow} from "@generated/vsan-test-row";

@Component({
   templateUrl: "test-details.html",
   selector: "vsan-tests-details",
   styleUrls: ["test-details.scss"],
})
export class TestDetailsComponent {
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;
   VsanHealthUtil: typeof VsanHealthUtil = VsanHealthUtil;
   readonly DEFAULT_ITEMS_PER_PAGE: number = 10;

   private _detailsArray: VsanTestTable[];

   @Input()
   loading: boolean;

   @Input()
   set detailsArray(data: VsanTestTable[]) {
      if (VsanUiUtils.isEmpty(data)) {
         this._detailsArray = [];
         return;
      }
      if (VsanUiUtils.isEmpty(this._detailsArray)) {
         this._detailsArray = data;
         return;
      }
      this._detailsArray.map((item, index) => {
         item.rows = data[index].rows;
      });
   }

   get detailsArray(): VsanTestTable[] {
      return this._detailsArray;
   }

   get testResults(): VsanTestRow[] {
      return VsanUiUtils.isEmpty(this.detailsArray) ? null : this.detailsArray[0].rows;
   }

   getHealthStatusLabel(cellValue: any): string {
      switch (cellValue) {
         case VsanHealthStatus.yellow:
            return VsanUiUtils.getString("vsan.health.monitor.proactive.warningStatus");
         case VsanHealthStatus.green:
            return VsanUiUtils.getString("vsan.health.monitor.proactive.okStatus");
         case VsanHealthStatus.red:
            return VsanUiUtils.getString("vsan.health.monitor.proactive.errorStatus");
      }
   }
}
