import {VsanHealthStatus} from "@generated/vsan-health-status";
import {ProactiveTestData} from "@generated/proactive-test-data";
import {VsanTestTable} from "@generated/vsan-test-table";
import {VsanUiUtils} from "../../common/util/vsan-util";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";

/**
 * Get the data of proactive tests result
 */
export class ProactiveTestResult {
   name: string;
   type: ProactiveTestData.PerfTestType;
   tooltip: string;
   timestampLabel: string;
   status: VsanHealthStatus;
   statusLabel: string;
   details: VsanTestTable[];
   helpId: string;
   description: string;

   constructor(model: ProactiveTestData) {
      // If model.timestamp is not null, the test has been run. Otherwise, the test has never been run.
      switch (model.perfTestType) {
         case ProactiveTestData.PerfTestType.vmCreation:
            this.name = VsanUiUtils.getString("vsan.health.monitor.proactive.tests.vmCreation");
            this.tooltip = VsanUiUtils.getString("vsan.health.monitor.proactive.warning.vmCreation.desc");
            break;
         case ProactiveTestData.PerfTestType.multicast:
            this.name = VsanUiUtils.getString("vsan.health.monitor.proactive.tests.network.multicast");
            this.tooltip = VsanUiUtils.getString("vsan.health.monitor.proactive.warning.network.multicast.desc");
            break;
         case ProactiveTestData.PerfTestType.unicast:
            this.name = VsanUiUtils.getString("vsan.health.monitor.proactive.tests.network.unicast");
            this.tooltip = VsanUiUtils.getString("vsan.health.monitor.proactive.warning.network.unicast.desc");
            break;
      }

      this.type = model.perfTestType;
      this.timestampLabel = VsanDateTimeUtils.formatDate(model.timestamp);

      if (!model.generalData) {
         return;
      }

      this.description = model.generalData.testDescription;
      this.status = model.generalData.status;
      switch (model.generalData.status) {
         case VsanHealthStatus.red:
            this.statusLabel = VsanUiUtils.getString("vsan.health.monitor.proactive.errorStatus");
            break;
         case VsanHealthStatus.yellow:
            this.statusLabel = VsanUiUtils.getString("vsan.health.monitor.proactive.warningStatus");
            break;
         case VsanHealthStatus.green:
            this.statusLabel = VsanUiUtils.getString("vsan.health.monitor.proactive.okStatus");
            break;
         default:
            this.statusLabel = VsanUiUtils.getString("vsan.na.label");
            break;
      }

      // expected exact one execution
      if (model.generalData.instances && model.generalData.instances[0].details.length > 0) {
         // details includes the result of running test, every test has only one result,
         // so if the test has been run, details contains only one element.
         this.details = model.generalData.instances[0].details;
         for (const detail of this.details) {
            detail.rows = detail.rows || [];
         }
      }
   }

   static createEmptyVmCreationTestResult() {
      const testData: ProactiveTestData = ProactiveTestData.Factory.create(
            null, null, ProactiveTestData.PerfTestType.vmCreation, null);
      return new ProactiveTestResult(testData);
   }
}
