import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {ProactiveTestResult} from "./../model/proactive-test-result";
import {ProactiveTestData} from "@generated/proactive-test-data";
import {PerfStatsObjectInfo} from "@generated/perf-stats-object-info";
import {VsanPerfPropertyProvider} from "@generated/vsan-perf-property-provider";
import {VsanPerfMutationProvider} from "@generated/vsan-perf-mutation-provider";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {PerfStatesObjSpec} from "@generated/perf-states-obj-spec";
import {CsdService} from "@generated/csd-service";

@Component({
   templateUrl: "run-test-dialog.html",
   styleUrls: ["run-test-dialog.scss"],
   selector: "vsan-run-test-dialog",
})
export class RunTestDialogComponent {
   private static readonly MODAL_NON_NETWORK_DIAGNOSTICS_HEIGHT: number = 230;

   dialogTitle: string;
   dialogQuestion: string;
   dialogContent: string;
   errorMessage: string[];
   perfSetting: PerfStatsObjectInfo;
   networkDiagnosticsModeAllowed: boolean;
   turnOnNetworkDiagnosticMode: boolean;
   loading: boolean = false;

   private isComputeOnlyCluster: boolean;
   private clusterCapabilityData: VsanCapabilityData;
   private selectedTest: ProactiveTestResult;

   constructor(private capabilityProvider: VsanCapabilityProvider,
         private perfPropertyProvider: VsanPerfPropertyProvider,
         private perfMutationProvider: VsanPerfMutationProvider,
         private csdService: CsdService) {
   }

   async ngOnInit(): Promise<void> {
      this.selectedTest = VsanUiUtils.getModalContext();
      this.dialogTitle = VsanUiUtils.getString("vsan.health.monitor.proactive.warning.title",
            this.selectedTest.name);
      switch (this.selectedTest.type) {
         case ProactiveTestData.PerfTestType.vmCreation:
            this.dialogQuestion = VsanUiUtils.getString("vsan.health.monitor.proactive.warning.vmCreation.question");
            this.dialogContent = VsanUiUtils.getString("vsan.health.monitor.proactive.warning.vmCreation.desc");
            break;
         case ProactiveTestData.PerfTestType.unicast:
            VsanUiUtils.setModalDialogHeight(360);

            this.dialogQuestion =
                  VsanUiUtils.getString("vsan.health.monitor.proactive.warning.network.unicast.question");
            this.dialogContent =
                  VsanUiUtils.getString("vsan.health.monitor.proactive.warning.network.unicast.desc");
            this.loading = true;
            [this.perfSetting, this.clusterCapabilityData, this.isComputeOnlyCluster] = await Promise.all([
               this.perfPropertyProvider.getPerfStatsInfo(ManagedObject.contextObject),
               this.capabilityProvider.getClusterCapabilityData(ManagedObject.contextObject),
               this.csdService.isComputeOnlyCluster(ManagedObject.contextObject)]);
            this.determineNetworkDiagnosticsModeStatus();
            this.loading = false;
            break;
      }
   }

   private determineNetworkDiagnosticsModeStatus(): void {
      // Set as turned on by default. The only exception is compute-only cluster.
      this.turnOnNetworkDiagnosticMode = !this.isComputeOnlyCluster;
      this.networkDiagnosticsModeAllowed = !this.isComputeOnlyCluster &&
            this.selectedTest.type === ProactiveTestData.PerfTestType.unicast &&
            this.clusterCapabilityData && this.clusterCapabilityData.isPerfDiagnosticModeSupported;
      if (!this.networkDiagnosticsModeAllowed) {
         VsanUiUtils.setModalDialogHeight(RunTestDialogComponent.MODAL_NON_NETWORK_DIAGNOSTICS_HEIGHT);
      }
   }

   get canEnableNetworkDiagnosticMode(): boolean {
      return this.perfSetting && this.perfSetting.serviceEnabled && !this.perfSetting.networkDiagnosticModeEnabled;
   }

   get disabledReason(): string {
      const perfEnabled: boolean = this.perfSetting && this.perfSetting.serviceEnabled;
      if (!perfEnabled) {
         return VsanUiUtils.getString("vsan.health.monitor.proactive.network.diagnostic.perf.disabled");
      }
      return VsanUiUtils.getString("vsan.health.monitor.proactive.network.diagnostic.alreadyOn");
   }

   onOkClicked = (): void => {
      const perfEnabled: boolean = this.perfSetting && this.perfSetting.serviceEnabled;
      if (this.networkDiagnosticsModeAllowed && this.turnOnNetworkDiagnosticMode
            && perfEnabled && !this.perfSetting.networkDiagnosticModeEnabled) {
         const perfSpec: PerfStatesObjSpec = PerfStatesObjSpec.Factory.create(ManagedObject.contextObject,
               this.perfSetting.spbmProfileUuid,
               this.perfSetting.verboseModeEnabled,
               this.turnOnNetworkDiagnosticMode);
         this.perfMutationProvider.editPerfConfiguration(perfSpec)
               .then(() => VsanUiUtils.closeModalDialog(null))
               .catch(err => this.errorMessage = err);
         return;
      }
      VsanUiUtils.closeModalDialog(null);
   };
}
