import {NgModule} from "@angular/core";
import {RouterModule, Routes} from "@angular/router";
import {CommonModule} from "@angular/common";
import {ClarityModule} from "@clr/angular";
import {VsanCommonModule} from "../common/vsan-common.module";
import {ProactiveTestsComponent} from "./view/proactive-tests.component";
import {TestDetailsComponent} from "./view/test-details.component";
import {RunTestDialogComponent} from "./view/run-test-dialog.component";
import {FormsModule} from "@angular/forms";
import {UnavailableViewModule} from "@component/unavailable-view/unavailable-view.module";

const proactiveTestsRoutes: Routes = [
   {
      path: "",
      component: ProactiveTestsComponent,
   },
   {
      path: "runProactiveTest",
      component: RunTestDialogComponent,
   },
];

@NgModule({
   imports: [
      FormsModule,
      CommonModule,
      ClarityModule,
      VsanCommonModule,
      UnavailableViewModule,
      RouterModule.forChild(proactiveTestsRoutes),
   ],
   declarations: [
      TestDetailsComponent,
      RunTestDialogComponent,
      ProactiveTestsComponent,
   ],

})
export class ProactiveTestsModule {
}
