import {Component, OnInit} from "@angular/core";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {RefreshHandler} from "@service/global-refresh.service";
import {ProactiveTestData} from "@generated/proactive-test-data";
import {ProactiveTestsService} from "@generated/proactive-tests-service";
import {ProactiveTestResult} from "../model/proactive-test-result";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {CsdService} from "@generated/csd-service";
import {MountedRemoteDatastore} from "@generated/mounted-remote-datastore";
import {TreeNode} from "@component/inventory/tree-view/tree-node";
import {InventoryNode} from "@generated/inventory-node";
import {ModalBuilder} from "@util/modal-builder";
import {TaskMonitorService} from "@service/task-monitor-service";
import ObjectInfo from "@component/inventory/tree-view/ObjectInfo";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";

@Component({
   templateUrl: "proactive-tests.html",
   styleUrls: ["proactive-tests.scss"],
   selector: "vsan-proactive-tests",
})
export class ProactiveTestsComponent implements OnInit {
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;
   VsanHealthUtil: typeof VsanHealthUtil = VsanHealthUtil;
   PerfTestType: typeof ProactiveTestData.PerfTestType = ProactiveTestData.PerfTestType;
   readonly HCIBENCH_SITE: string = "https://labs.vmware.com/flings/hcibench";
   readonly COMMUNITY_SITE: string = "https://communities.vmware.com/community/vmtn/vsan";
   private static readonly RUNNING_TEST_KEY_SEPARATOR: string = ":";

   // The timeout time for the VM creation test, its value should be greater than 190.
   private readonly TIMEOUT: number = 200;

   // The value of runningTests and loadingTests looks like {"vmCreation", "unicast"} when CSD is not supported,
   // or {"datastore-65:vmCreation", "datastore-66:unicast"} when CSD is supported.
   private runningTests: Set<string> = new Set();
   private loadingTests: Set<string> = new Set();

   loading: boolean = true;
   showReload: boolean = false;
   alertMessage: string;
   unavailableView: UnavailableViewSpec;
   hasEditPermissions: boolean = false;

   testTypeToResultMap: Map<ProactiveTestData.PerfTestType, ProactiveTestResult> =
         new Map<ProactiveTestData.PerfTestType, ProactiveTestResult>();
   testTypeToDatastoreObjectInfoMap: Map<ProactiveTestData.PerfTestType, InventoryNode> =
         new Map<ProactiveTestData.PerfTestType, InventoryNode>();

   isCsdSupported: boolean = false;
   mountedDatastores: MountedRemoteDatastore[];
   datastoreTreeNodes: TreeNode[];

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   constructor(private proactiveTestsService: ProactiveTestsService,
         private permissionService: PermissionService,
         private csdService: CsdService,
         private taskMonitorService: TaskMonitorService) {
   }

   async ngOnInit(): Promise<void> {
      this.isCsdSupported = await this.csdService.isCsdSupported(ManagedObject.contextObject);
      this.fetch();
   }

   @RefreshHandler
   async fetch(): Promise<void> {
      this.loading = true;
      this.alertMessage = null;
      this.permissionService.hasPermissions(ManagedObject.contextObject, [Privileges.EDIT_CLUSTER()])
            .then(result => this.hasEditPermissions = result);
      this.mountedDatastores = (await this.csdService.getMountedDatastores(ManagedObject.contextObject))
            .filter((ds: MountedRemoteDatastore) => ds.shareableDatastore.serverCluster);
      if (!this.isCsdSupported) {
         this.mountedDatastores = this.mountedDatastores.filter((ds: MountedRemoteDatastore) => ds.isLocal);
      }
      this.datastoreTreeNodes = this.getDatastoreTreeNodes();

      if (VsanUiUtils.isEmpty(this.mountedDatastores)) {
         this.unavailableView = UnavailableViewSpec.PROACTIVE_TESTS_NO_DATASTORE;
      } else {
         this.unavailableView = null;
         this.getAllTestResults();
      }
      this.loading = false;
   }

   private async getAllTestResults(): Promise<void> {
      try {
         const selectedDatastoreRef: ManagedObject = VsanUiUtils.isEmpty(this.mountedDatastores) ? null :
               this.mountedDatastores[0].shareableDatastore.datastore.moRef;
         const selectedServerClusterRef: ManagedObject = VsanUiUtils.isEmpty(this.mountedDatastores) ? null :
               this.mountedDatastores[0].shareableDatastore.serverCluster.moRef;
         const results: ProactiveTestData[] = await this.proactiveTestsService.getProactiveTestResults(
               ManagedObject.contextObject, selectedDatastoreRef, selectedServerClusterRef);
         // Initialize the proactive tests page, the result is an array in this case
         this.handleAllTestResults(results);
      } catch (err) {
         this.onCatch(err);
      } finally {
         this.loading = false;
      }
   }

   private handleAllTestResults(results: ProactiveTestData[]): Promise<void> {
      if (VsanUiUtils.isEmpty(results)) {
         this.showReload = true;
         this.alertMessage = VsanUiUtils.getString("vsan.health.monitor.proactive.no.data");
         return;
      }
      const selectedDatastoreObjectInfo: InventoryNode = !VsanUiUtils.isEmpty(this.datastoreTreeNodes)
            ? this.datastoreTreeNodes[0].objectInfo as InventoryNode
            : null;
      results.forEach(item => {
         this.testTypeToResultMap.set(item.perfTestType, new ProactiveTestResult(item));
         this.testTypeToDatastoreObjectInfoMap.set(item.perfTestType, selectedDatastoreObjectInfo);
      });
      this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
            VsanUiUtils.getString("vsan.health.monitor.proactive.title"));
   }

   private onCatch = (error: any) => {
      this.showReload = true;
      this.alertMessage = VsanUiUtils.getString("vsan.health.monitor.proactive.no.data");
      Logger.error("Unable to query Proactive Test History data: " + error);
   };

   get displaySpinner(): boolean {
      return this.loading && this.testTypeToResultMap.size === 0;
   }

   displayTestDetailsSpinner(type: ProactiveTestData.PerfTestType): boolean {
      return type && (this.loading || this.isRunningTest(type) || this.isLoadingTest(type));
   }

   isRunTestButtonDisabled(type: ProactiveTestData.PerfTestType): boolean {
      return !this.hasEditPermissions || this.displayTestDetailsSpinner(type) ||
            VsanUiUtils.isEmpty(this.mountedDatastores);
   }

   get runTestButtonTitle(): string {
      if (VsanUiUtils.isEmpty(this.mountedDatastores)) {
         return VsanUiUtils.getString("vsan.health.monitor.proactive.runTest.noDatastores");
      }
      return VsanUiUtils.getActionTitle(this.hasEditPermissions, "vsan.health.monitor.proactive.runTest");
   }

   get selectorVisible(): boolean {
      return this.isCsdSupported && !VsanUiUtils.isEmpty(this.mountedDatastores) &&
            this.mountedDatastores.some((ds: MountedRemoteDatastore) => !ds.isLocal);
   }

   openRunTestDialog(selectedTest: ProactiveTestResult): void {
      new ModalBuilder("proactiveTests/runProactiveTest")
            .setSize(670, 260)
            .open(selectedTest)
            .then(() => this.runTest(selectedTest));
   }

   private runTest(selectedTest: ProactiveTestResult): void {
      // After the task is completed, we use dataStoreRef and test type to delete the test from the running test list,
      // store dataStoreRef to a new variable to avoid its value being changed after switching to another datastore.
      const dataStoreRef: ManagedObject = this.getSelectedDatastoreRef(selectedTest.type);
      this.runningTests.add(this.getRunningTestData(dataStoreRef, selectedTest.type));
      if (selectedTest.type === ProactiveTestData.PerfTestType.vmCreation) {
         this.proactiveTestsService.getVMCreationTestResult(
               ManagedObject.contextObject, this.TIMEOUT, dataStoreRef)
               .then(data => this.runTestSucceeded(data, dataStoreRef))
               .catch(error => this.runTestFailed(error, selectedTest.name, ProactiveTestData.PerfTestType.vmCreation,
                     dataStoreRef));
      } else {
         if (this.isNetworkTestAgainstLocalCluster(selectedTest.type)) {
            const isTestMulticast: boolean = selectedTest.type === ProactiveTestData.PerfTestType.multicast;
            this.proactiveTestsService.getNetworkPerfTestResult(ManagedObject.contextObject, isTestMulticast)
                  .then(data => this.runTestSucceeded(data, dataStoreRef))
                  .catch(error => this.runTestFailed(error, selectedTest.name, ProactiveTestData.PerfTestType.unicast,
                        dataStoreRef));
         } else {
            this.proactiveTestsService.startNetworkPerfTestTask(
                  ManagedObject.contextObject, this.getSelectedServerClusterRef(selectedTest.type))
                  .then(task => this.handleNetworkTestTask(task, dataStoreRef, selectedTest.type))
                  .catch(error => this.runTestFailed(error, selectedTest.name, ProactiveTestData.PerfTestType.multicast,
                        dataStoreRef));
         }
      }
   }

   private async handleNetworkTestTask(task: ManagedObject, datastoreRef: ManagedObject,
         type: ProactiveTestData.PerfTestType): Promise<void> {
      await this.taskMonitorService.getAwaitResult(task);
      const networkTestData: ProactiveTestData = await this.proactiveTestsService.getLastNetworkTestResult(
            ManagedObject.contextObject, this.getSelectedServerClusterRef(type));
      this.runTestSucceeded(networkTestData, datastoreRef);
   }

   private runTestSucceeded = (result: ProactiveTestData, dataStoreRef: ManagedObject) => {
      this.runningTests.delete(this.getRunningTestData(dataStoreRef, result.perfTestType));
      if (!this.isCsdSupported ||
            ManagedObject.areEqual(this.getSelectedDatastoreRef(result.perfTestType), dataStoreRef)) {
         this.handleSingleTestResult(result);
      }
   };

   private handleSingleTestResult = (result: ProactiveTestData) => {
      if (!result) {
         this.showReload = true;
         this.alertMessage = VsanUiUtils.getString("vsan.health.monitor.proactive.no.data");
         return;
      }
      this.testTypeToResultMap.set(result.perfTestType, new ProactiveTestResult(result));
   };

   private runTestFailed = (error: any, name: string, testType: ProactiveTestData.PerfTestType,
         dataStoreRef: ManagedObject) => {
      this.showReload = false;
      this.runningTests.delete(this.getRunningTestData(dataStoreRef, testType));
      this.alertMessage = VsanUiUtils.getString("vsan.health.monitor.proactive.running.error", name);
      Logger.error(`Unable to run ${name}: ` + error);
   };

   private isNetworkTestAgainstLocalCluster(type: ProactiveTestData.PerfTestType): boolean {
      const mountedDatastore: MountedRemoteDatastore = this.getSelectedMountedRemoteDatastore(type);
      return mountedDatastore ? mountedDatastore.isLocal : true;
   }

   isRunningTest(type: ProactiveTestData.PerfTestType): boolean {
      return this.runningTests.has(this.getRunningTestData(this.getSelectedDatastoreRef(type), type));
   }

   private getRunningTestData(dataStoreRef: ManagedObject, testType: ProactiveTestData.PerfTestType): string {
      return dataStoreRef ?
            dataStoreRef.value + ProactiveTestsComponent.RUNNING_TEST_KEY_SEPARATOR + testType.toString() :
            testType.toString();
   }

   getSelectedDatastoreObjectInfosBasedOnType(type: ProactiveTestData.PerfTestType): InventoryNode[] {
      return [this.testTypeToDatastoreObjectInfoMap.get(type)];
   }

   onSelectedDatastoreObjectInfosChange(objectInfos: ObjectInfo[], type: ProactiveTestData.PerfTestType): void {
      const datastoreRef: ManagedObject = this.getSelectedDatastoreRef(type);
      if ((!datastoreRef && VsanUiUtils.isEmpty(objectInfos)) ||
            ManagedObject.areEqual(datastoreRef, objectInfos[0].moRef)) {
         return;
      }
      this.testTypeToDatastoreObjectInfoMap.set(type, objectInfos[0] as InventoryNode);
      this.fetchLastTestResult(type);
   }

   private async fetchLastTestResult(type?: ProactiveTestData.PerfTestType): Promise<void> {
      const datastoreRef: ManagedObject = this.getSelectedDatastoreRef(type);
      this.loadingTests.add(this.getRunningTestData(datastoreRef, type));
      // this.runningTests.add(this.getRunningTestData(datastoreRef, type));
      if (type === ProactiveTestData.PerfTestType.vmCreation) {
         const vmCreationTestResult: ProactiveTestData = await this.proactiveTestsService.getLastVmCreationTestResult(
               ManagedObject.contextObject, datastoreRef);
         this.loadTestSucceeded(vmCreationTestResult, datastoreRef);
      } else {
         const networkTestResult: ProactiveTestData = await this.proactiveTestsService.getLastNetworkTestResult(
               ManagedObject.contextObject, this.getSelectedServerClusterRef(type));
         this.loadTestSucceeded(networkTestResult, datastoreRef);
      }
   }

   private loadTestSucceeded(result: ProactiveTestData, dataStoreRef: ManagedObject): void {
      this.loadingTests.delete(this.getRunningTestData(dataStoreRef, result.perfTestType));
      if (!this.isCsdSupported ||
            ManagedObject.areEqual(this.getSelectedDatastoreRef(result.perfTestType), dataStoreRef)) {
         this.handleSingleTestResult(result);
      }
   }

   isLoadingTest(type: ProactiveTestData.PerfTestType): boolean {
      return type && this.loadingTests.has(this.getRunningTestData(this.getSelectedDatastoreRef(type), type));
   }

   displayTestResult(testResult: ProactiveTestResult): boolean {
      return testResult && !this.isLoadingTest(testResult.type) && !!testResult.status;
   }

   getSelectedDatastoreRef(type: ProactiveTestData.PerfTestType): ManagedObject | null {
      const selectedDatastoreObjectInfo: InventoryNode = this.testTypeToDatastoreObjectInfoMap.get(type);
      return selectedDatastoreObjectInfo ? selectedDatastoreObjectInfo.moRef : null;
   }

   getSelectedServerClusterRef(type: ProactiveTestData.PerfTestType): ManagedObject | null {
      const mountedDatastore: MountedRemoteDatastore = this.getSelectedMountedRemoteDatastore(type);
      return mountedDatastore ? mountedDatastore.shareableDatastore.serverCluster.moRef : null;
   }

   getSelectedMountedRemoteDatastore(type: ProactiveTestData.PerfTestType): MountedRemoteDatastore | null {
      const selectedDatastoreObjectInfo: InventoryNode = this.testTypeToDatastoreObjectInfoMap.get(type);
      if (!selectedDatastoreObjectInfo) {
         return null;
      }
      return this.mountedDatastores.find(mountedDs => ManagedObject.areEqual(
            mountedDs.shareableDatastore.datastore.moRef, selectedDatastoreObjectInfo.moRef));
   }

   private getDatastoreTreeNodes(): TreeNode[] {
      return this.mountedDatastores.map((ds: MountedRemoteDatastore) => {
         const clonedDS: MountedRemoteDatastore = VsanUiUtils.deepClone(ds);
         clonedDS.shareableDatastore.datastore.name = VsanUiUtils.getString("vsan.csd.datastoreWithServerCluster",
               clonedDS.shareableDatastore.datastore.name, clonedDS.shareableDatastore.serverCluster.name);
         return new TreeNode(clonedDS.shareableDatastore.datastore, null);
      });
   }
}
